    b.exports = { 1: { section: "ARCHIVES", title: "Saigon Execution", text: "[From Wikipedia] It was while covering the Vietnam War for the Associated Press that he took his best-known photograph  the picture of police chief General Nguyn Ngc Loan executing a Vietcong prisoner, Nguyn Vn Lm, on a Saigon street, on February 1, 1968, during the opening stages of the Tet Offensive. Adams won the 1969 Pulitzer Prize for Spot News Photography and a World Press Photo award for the photograph (captioned 'General Nguyen Ngoc Loan executing a Viet Cong prisoner in Saigon'), but would later lament its notoriety.[6] Writer and critic David D. Perlmutter points out that 'no film footage did as much damage as AP photographer Eddie Adams's 35mm shot taken on a Saigon street ... When people talk or write about [the Tet Offensive] at least a sentence is devoted (often with an illustration) to the Eddie Adams picture'.[7] Anticipating the impact of Adams's photograph, an attempt at balance was sought by editors in the New York Times. In his memoirs,[8] John G. Morris recalls that assistant managing editor Theodore M. Bernstein \"determined that the brutality manifested by America's ally be put into perspective, agreed to run the Adams picture large, but offset with a picture of a child slain by Vietcong, which conveniently came through from AP at about the same time\". Nonetheless, it is Adams's photograph that is remembered while the other far less dramatic image was overlooked and soon forgotten. In Regarding the Pain of Others, Susan Sontag is disturbed by what she sees as the staged nature of the photograph. She writes that 'he would not have carried out the summary execution there had [the press] not been available to witness it'.[9] However, Donald Winslow of the New York Times quotes Adams as having described the image as a 'reflex picture' and 'wasn't certain of what he'd photographed until the film was developed'. Furthermore, Winslow notes that Adams 'wanted me to understand that Saigon Execution was not his most important picture and that he did not want his obituary to begin, Eddie Adams, the photographer best known for his iconic Vietnam photograph Saigon Execution'.", src: "img/1-1.jpg", author: "Eddie Adams/ Associated Press", capture: "Police Chief General Nguyen Ngoc Loan executing a Vietcong officer captured in Saigon, February 1, 1968.", context: { selectedIndex: 1, items: [{ src: "img/1-0.jpg", credit: "Eddie Adams/AP" }, { src: "img/1-1.jpg", credit: "Eddie Adams/AP" }, { src: "img/1-2.jpg", credit: "Eddie Adams/AP" }, { youtube_id: "S9Jy3cLmqrE", text: "MrCleatsketch. YouTube." }] }, links: [{ title: "General Loan", href: "https://en.wikipedia.org/wiki/Nguy%E1%BB%85n_Ng%E1%BB%8Dc_Loan", type: "wikipedia-w" }, { title: "Vietcong Officer", href: "https://en.wikipedia.org/wiki/Viet_Cong", type: "wikipedia-w" }, { title: "Vietcong agent executed...", href: "https://www.youtube.com/watch?v=H4yZCaeboCs", type: "play-circle-o" }], backStory: { text: "The general killed the Viet Cong; I killed the general with my camera. Still photographs are the most powerful weapon in the world. People believe them, but photographs do lie, even without manipulation. They are only half-truths. What the photograph didnt say was, What would you do if you were the general at that time and place on that hot day, and you caught the so-called bad guy after he blew away one, two or three American soldiers?... Im not saying what he did was right, but you have to put your self in his position. So the photograph also doesnt say that the general devoted much of his time trying to get hospitals built in Vietnam for war casualties. This picture really messed up his life. He never blamed me. He told if I hadnt taken the picture, someone else would have, but I felt bad for him and his family for a long time.", author: "Eddie Adams", magazine: "Time magazine", magazineUrl: "http://time.com/", date: "June 24, 2001" }, creativeCommons: { copyright: "Eddie Adams/ Associated Press", codeOfEthics: "", description: "Police Chief General Nguyen Ngoc Loan executing a Vietcong officer captured in Saigon, February 1, 1968." } }, 2: { section: "MIDDLE EAST", title: "Drowned 3 year old Syrian, Aylan Kurdi", text: "[From Wikipedia] Alan Kurdi (Kurdish: Alan Kurd), initially reported as Aylan Kurdi,[1][2] was a three-year-old Syrian boy of Kurdish ethnic background[3] whose image made global headlines after he drowned on 2 September 2015 in the Mediterranean Sea, as part of the Syrian refugee crisis. Photographs of his body were taken by Turkish journalist Nilfer Demir and quickly spread around the world, prompting international responses.[4] Because Kurd's family had reportedly been trying to reach Canada, his death and the wider refugee crisis immediately became an issue in the 2015 Canadian federal election. In the early hours of 2 September 2015, Kurdi and his family boarded a small plastic or rubber inflatable boat,[18] which capsized about five minutes after leaving Bodrum in Turkey. Sixteen people were in the boat, which was designed for a maximum of eight people. They were trying to reach the Greek island of Kos, about 30 minutes (4 kilometres or 2 12 miles) from Bodrum.[18] Kurdi's father said: \"We had no life vests\",[19] but also said they were wearing life jackets, but they \"were all fake\".[20] Others have stated that they believed that they were wearing lifejackets but the items were ineffective.[5] It was later stated on Syrian radio that the Kurdi family paid $5,860 for their four spaces on the boat, which had twelve passengers on it despite being only about five meters long. Alan Kurdi's mother joined in the trip even though she had a fear of being on the open sea.[21] Tima Kurdi, Alan Kurdi's aunt, advised her sister not to go. The individuals on the boat evaded the Turkish coast guard by setting out from an isolated beach, going out late at night.[5] Around 5 am authorities started an investigation, after an emergency call that a boat had capsized and bodies were coming ashore.[22] The bodies of Kurdi and another child were discovered by two locals at around 6:30 am on the same morning.[23] The two men moved the bodies from the water onto the dry part of the beach.[23] Later, Kurdi was photographed face-down on the water's edge by a Turkish press photographer.[23][24][25] On 3 September 2015, Kurdi along with brother Galib and mother Rehana were taken to Koban for burials,[26] which took place the next day. It is Islamic tradition to bury the dead within 24 hours if possible.[27] The Siege of Koban ended in March 2015 and Islamic State attacks on what was left of the city[28] stopped completely in August 2015.", src: "img/2-1.jpg", author: "Nilfer Demir/DHA", capture: "A Turkish officer near the body of Alan Kurdi, a 3-year-old Syrian refugee who drowned off Turkeys Bodrum Peninsula. The body of his brother, Ghalib, washed up nearby. September 1, 2015.", context: { selectedIndex: 1, items: [{ src: "img/2-0.jpg", text: "Alan Kurdi (left) and his older brother, Ghalib, died when their dinghy sank off the coast of Turkey.", credit: "Alan Kurdi (3), left, with his older brother Ghalib (5). Qattouby/Twitter" }, { src: "img/2-1.jpg", text: "A Turkish police officer stands next to the body of the young boy.", credit: "Nilufer Demir/DHA, via Reuters" }, { src: "img/2-2.jpg", text: "A Turkish police officer carries a young boy who drowned in a failed attempt to sail to the Greek island of Kos. Photograph: Reuters", credit: "Nilufer Demir/DHA, via Reuters" }] }, links: [{ title: "Alan Kurdi: Why one picture cut through", href: "http://www.bbc.co.uk/news/world-europe-34150419", type: "newspaper-o" }, { title: "Dispatches: Why I Shared a Horrific Photo of a Drowned Syrian Child", href: "https://www.hrw.org/news/2015/09/02/dispatches-why-i-shared-horrific-photo-drowned-syrian-child", type: "newspaper-o" }, { title: "Death of Alan Kurdi", href: "https://en.wikipedia.org/wiki/Death_of_Alan_Kurdi", type: "wikipedia-w" }], backStory: { text: 'On the one hand, I wish I hadnt had to take that picture. I would have much preferred to have taken one of Alan playing on the beach than photographing his corpse. What I saw has left a terrible impression that keeps me awake at night. Then again, I am happy that the word finally cares and is mourning the dead children. I hope that my picture can contribute to changing the way we look at immigration in Europe, and that no more people have to die on their way out of a war."', author: "Nilfer Demir", magazine: "Vice", magazineUrl: "https://www.vice.com/en_uk/read/nilfer-demir-interview-876", date: "September 4, 2015" }, creativeCommons: { copyright: "Nilfer Demir/DHA  2015", codeOfEthics: "CODE OF ETHICS: While all photography is interpretive, as a photojournalist I consider my photographs are meant to respect the visible facts of the situations I depict. I do not add or subtract elements to or from my photographs.", description: "A Turkish officer near the body of Alan Kurdi, a 3-year-old Syrian refugee who drowned off Turkeys Bodrum Peninsula. The body of his brother, Ghalib, washed up nearby. September 1, 2015." } } };
